.sitecontent {
  max-width: 1200px;
  width: 100%;
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
}
.innercontent {
  margin-top: 150px;
  min-height: 800px;
}
.btn-light {
  background-color: #d0d0d0 !important;
  border: solid 1px #d0d0d0 !important;
  color: white !important;
}
.btn-light:hover {
  background-color: #434343 !important;
}
.btn-footerdark {
  background-color: #919191 !important;
  border: solid 1px #919191 !important;
  color: white;
}
.btn-footerdark:hover {
  background-color: #00a2ff !important;
}
.btn-cerca {
  background-image: url('/images/common/search.png');
  background-repeat: no-repeat;
  width: 45px;
  background-position: 5px 3px;
  border-right: solid 2px white !important;
}
.btn-cerca:hover {
  background-color: #434343 !important;
}
.btn-more {
  background-color: #c0c0c0;
  padding-right: 55px !important;
  background-image: url("/images/common/ellipsis.png");
  background-position: right 15px !important;
  background-repeat: no-repeat;
  color: white !important;
  text-transform: uppercase;
  font-weight: bold;
}
.btn-more:hover {
  color: #00a2ff;
}
.btn-goup {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 65px;
  height: 65px;
  cursor: pointer;
  background-image: url("/images/common/goup.png");
  background-size: 100%;
  background-repeat: no-repeat;
  display: none;
  z-index: 999999;
}
.btsocial {
  height: 50px;
  width: 50px;
  cursor: pointer;
  display: inline-block;
  margin: 10px;
}
@media screen and (min-width: 770px) and (max-width: 980px) {
  .btsocial {
    margin: 5px;
    height: 35px;
    width: 35px;
  }
  .btsocial rect {
    height: 40px;
    width: 40px;
  }
  .btsocial image {
    width: 30px;
    height: 30px;
    transform: translate(-5px, -5px) !important;
  }
}
.btsocial image {
  width: 30px;
  height: 30px;
}
.btsocial .socialbtbox {
  fill: #919191;
  transition: 1s all ease;
}
.btsocial:hover .socialbtbox {
  fill: #00a2ff;
}
.headertop {
  border-bottom: solid 1px #dcdcdc;
  padding-top: 10px;
  padding-bottom: 10px;
}
.btn-radius-right {
  border-radius: 0 5px 5px 0  !important;
}
.btn-menumobile {
  background-image: url("/images/common/btmenumobile.png");
  background-repeat: no-repeat;
  cursor: pointer;
  width: 48px;
  background-position-x: right;
}
.tendinamenu {
  position: fixed;
  width: 100%;
  max-width: 450px;
  height: 100%;
  top: 0;
  right: -450px;
  z-index: 1;
  background-color: white;
  text-align: center;
  overflow: hidden;
  border-left: solid 1px #dcdcdc;
}
@media screen and (max-width: 450px) {
  .tendinamenu {
    border-left: solid 1px #ffffff;
  }
}
.tendinamenu .menumobileframeback {
  margin: -15px -15px 20px -15px;
  background-color: #dcdcdc;
  padding: 15px;
  text-transform: uppercase;
  font-weight: bold;
  background-image: url("/images/common/arrowback.png");
  background-repeat: no-repeat;
  background-position: 15px center;
}
.tendinamenu .menumobileframe {
  border-left: solid 1px #dcdcdc;
  background-color: white;
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 130px;
  right: -450px;
  padding: 20px;
}
.tendinamenu .menumobileframe:first-child {
  right: 0px;
  border: none;
}
.tendinamenu .menumobileframe li a {
  display: block;
  padding: 5px;
  border-bottom: dashed 1px #dcdcdc;
  color: #434343;
  text-transform: uppercase;
}
.tendinamenu .menumobileframe li:last-child a {
  border: none;
}
.headermenuwrapper .topmenu {
  margin-top: 15px;
  margin-bottom: 15px;
}
.headermenuwrapper .topmenu li a {
  color: #434343 !important;
  font-size: 14px;
  display: block;
  padding: 0 10px 10px 0;
  transition: 0.5s all ease;
}
.headermenuwrapper .topmenu li a:hover {
  text-decoration: none;
  color: #00a2ff !important;
}
@media screen and (max-width: 900px) {
  .headermenuwrapper .topmenu li a {
    font-size: 12px !important;
  }
}
.headermenuwrapper .topmenu .nav-item:hover .nav-submenu {
  display: block;
}
.headermenuwrapper .topmenu .nav-submenu {
  display: none;
  position: absolute;
  padding: 20px;
  background-color: white;
  z-index: 100;
  border-radius: 0 0 5px 5px;
  min-width: 250px;
  border-left: solid 1px #dcdcdc;
  border-right: solid 1px #dcdcdc;
  border-bottom: solid 1px #dcdcdc;
}
.headermenuwrapper .topmenu .nav-submenu a {
  display: block;
  padding: 0 0 5px 0;
  margin: 0 0 5px 0;
  border-bottom: dashed 1px #dcdcdc;
  text-align: left;
  font-size: 13px;
}
.headermenuwrapper .topmenu .nav-submenu a:last-child {
  border: none;
}
.headermenuwrapper .topmenu .nav-submenu a:first-child {
  margin-top: 10px;
}
.header {
